.container{
  text-align: center;
}

.title{
  margin-top: 50px;
  color: white;
  font-size: 40px;
  justify-content: center;
  align-items: center;
}
.title span{
  color: #26ffcb;
  padding-left: 15px;
}
.title img{
  height: 50px;
}

.reset{
  width: 100px;
  height: 40px;
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 50px;
  background: #1f3540;
  font-size: 20px;
  color: #26ffcb;
  margin-top: 25px;
  margin-bottom: 50px;
}

.boxes{
  display: flex;
  height: 100px;
  width: 100px;
  background: #1f3540;
  border: 4px solid #0f1b21;
  border-radius: 12px;
  cursor: pointer;
}
.boxes img{
  margin: 25px;
}

.board{
  justify-content: center;
  display: flex;
  margin: auto;
}
